@include susy-media(phone-only) {
	.frontpage>.rooms-section>.rooms {
		margin-top: 0;
	}

	.rooms {
		margin-top: 6rem;
	}
}

.mobile-title-section {
	display: none;
	position: fixed;
	z-index: 5;
	background: rgba(255, 255, 255, 0.65);
	top: 56px;
	width: 100%;
	left: 0; // Ensure it starts at left edge
	text-align: center;

	@include susy-media(phone-only) {
		display: block;
	}

	.title {
		padding: 10px;
		font-size: 32px;
		font-weight: 700;
		color: #4a4a4a;
	}
}

.description {
	color: #8a8a8a;

	.title {
		@extend h1;
		font-weight: 700;
		color: #4a4a4a;

		@include susy-media(phone-only) {
			display: none;
		}
	}
}

.facilities {
	.component-header {
		width: span(6 of 12);
		color: #e7ad44;
		line-height: 1.6em;
		font-size: 18px;
		position: relative;
		z-index: 99;
		padding-left: 70px;
		margin: 30px 0;
	}

	.component-title {
		font-weight: bold;
		text-transform: uppercase;
	}

	.component-header:before {
		border-top: 2px solid #e7ad44;
		width: 60px;
		content: "";
		display: inline-block;
		vertical-align: middle;
		position: absolute;
		top: 50%;
		left: 0;
	}

	.content-facilities {
		display: flex;
		flex-wrap: wrap;

		@include susy-media(phone-only) {
			flex-direction: column;
		}

		.facility {
			border-bottom: 1px solid #eee;
			flex-basis: span(4 of 16);
			display: flex;
			align-items: center;
			padding: 15px;
		}
	}

	.material-icons {
		color: #e7ad44;
		padding: 5px;
	}

}

.rates {
	.component-header {
		width: span(6 of 12);
		color: #e7ad44;
		line-height: 1.6em;
		font-size: 18px;
		position: relative;
		z-index: 99;
		padding-left: 70px;
		margin: 30px 0;
	}

	.component-title {
		font-weight: bold;
		text-transform: uppercase;
	}

	.component-header:before {
		border-top: 2px solid #e7ad44;
		width: 60px;
		content: "";
		display: inline-block;
		vertical-align: middle;
		position: absolute;
		top: 50%;
		left: 0;
	}

	.content {
		display: flex;
		flex-wrap: nowrap;
		justify-content: space-between;

		@include susy-media(phone-only) {
			flex-wrap: wrap;
		}

		.list-rates {
			min-width: span(4 of 12 0);
			display: flex;
			flex-direction: column;
			flex-wrap: wrap;
			border: 1px solid #e2e2e2;
			box-shadow: $elevation-shadow-2;

			@include susy-media(phone-only) {
				flex-grow: 1;
				margin-bottom: $post-spacing;
			}

			.group {
				display: flex;
				justify-content: space-between;
				align-items: center;
			}

			.top {
				color: #757575;
				font-size: 14px;
				display: flex;
				flex-wrap: wrap;

				.date1 {
					// border-right: 1px solid #e2e2e2;
					padding: 15px 10px;
					// min-width: span( 6 of 12 0);
				}

				.date2 {
					padding: 15px 10px;
					// min-width: span( 6 of 12 0);
				}
			}

			.bottom {
				background: #e7ad44;
				padding: 15px;
				margin-top: auto;

				.season {
					color: #585858;
					font-weight: bold;
				}

				.price {
					font-weight: bold;
					font-size: 28px;
					font-family: 'Merriweather', 'Cambria', 'Georgia', 'serif';
					color: #585858;
					flex: 1;
					text-align: center;
				}
			}

		}

		.list-rates:nth-child(3) .top {
			justify-content: flex-start;

			.date2 {
				padding: 15px 10px;
				min-width: auto;
			}
		}
	}

}

.rates {
	margin-bottom: $spacing-large;
}

.rooms {
	.contact-rooms--section {
		background: #f3f3f3;
		padding: 30px 30px 50px;
		// .contact {
		// 	@include susy-media(	tablet-landscape-up) {
		// 		padding: $section-spacing;
		// 	}	    	
		// }
	}
}

.reviews-rooms--section {
	position: relative;
	margin-bottom: 80px;

	.reviews-rooms {
		display: flex;
		flex-direction: column;

		.component-header {
			color: #e7ad44;
			line-height: 1.6em;
			font-size: 18px;
			position: relative;
			z-index: 99;
			padding-left: 70px;
			margin: 30px 0;
		}

		.component-header:before {
			border-top: 2px solid #e7ad44;
			width: 60px;
			content: "";
			display: inline-block;
			vertical-align: middle;
			position: absolute;
			top: 50%;
			left: 0;
		}

		.component-title {
			font-weight: bold;
			text-transform: uppercase;
		}

		.owl-carousel {
			border: 1px solid #DDA32D;
			width: span(12 of 12);
		}

		.review-room {
			font-size: 18px;
			margin: 18px 0;
			padding: 20px;
			font-style: italic;
			line-height: 28px;
			color: #8a8a8a;
			font-weight: 300;
			z-index: 99;
		}

		.review-text {
			margin-bottom: 20px;
		}

		.name,
		.country {
			display: inline;
			font-weight: bold;
		}
	}
}

.photos-rooms--section {
	.component-header {
		color: #e7ad44;
		line-height: 1.6em;
		font-size: 18px;
		position: relative;
		z-index: 99;
		padding-left: 70px;
		margin: 30px 0;
	}

	.component-header:before {
		border-top: 2px solid #e7ad44;
		width: 60px;
		content: "";
		display: inline-block;
		vertical-align: middle;
		position: absolute;
		top: 50%;
		left: 0;
	}

	.component-title {
		font-weight: bold;
		text-transform: uppercase;
	}

	.content {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;

		@include susy-media(phone-only) {
			justify-content: center;
		}

		.material-placeholder {
			@include susy-media(phone-only) {
				flex-grow: 1;
			}

			flex-basis: 270px;
			margin-bottom: gutters(6);
		}

		.material-placeholder:last-child {
			align-self: flex-start
		}
	}
}


// frontpage-mobile
@include susy-media(phone-only) {
	.rooms {
		display: flex;
		flex-direction: column;

		.slideshow-section {
			display: none;
		}

		.mobile-title-section {
			order: 1;
		}

		.description-section {
			order: 1;
		}

		.facilities-section {
			order: 4;
		}

		.rates-section {
			order: 3;
		}

		.contact-rooms--section {
			order: 6;
		}

		.reviews-rooms--section {
			order: 5;
		}

		.photos-rooms--section {
			order: 2;
		}

		.details-section {
			order: 6;
		}

		.footer-wrapper {
			order: 7;
		}
	}
}